import Evaluating from '@/components/common/Evaluating/components/myIssue.vue';
import assembly from './Opinion-assembly.vue';
import downArrow from '@/static/mbcImg/my/downArrow.png';
export default {
	data() {
		return {
			downArrow:downArrow,
			txtVal: 0,
			desc: '',
			remnane: 0,
			listData: {
				imageData: [],
				serverUrl: 'https://img01.iambuyer.com/imgup/upLoad/fileUpload' // 图片上传地址
			},
			logo:[],
			xing:0,//选中几个星星
			num:'0',//控制组件显示隐藏
			xin:'7',
			problem:[],
			id:'',
			feed:'',
		};
	},
	components: {
		Evaluating,
		assembly
	},
	computed:{
		i18n () {
		  return this.$t('Mypersonal')  
		},
	},
	methods: {
		// 通过子组件触发父组件的事件
		insuranceInformations () {
			this.assembly();
		},
		hiden(e){
			this.num='1';
			this.id = e.id; //反馈id
			this.feed = e.feedbackNum; //选择分数
			// console.log(this.id)
		},
		down(data){
			this.num=data;
		},
		assembly(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/userFeedBack?page=1&userId=760', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						// console.log(response.data);
						this.problem = response.data.content.list
						console.log(this.problem)
						// this.$store.commit('setMation', this.List); // 更新vuex
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	},
	created() {
		this.assembly();
	},
	filters: {
		ellipsis (value) {
		  if (!value) return ''
		  if (value.length > 40) {
			return value.slice(0,40) + '...'
		  }
		  return value
		}
	}
};
